"use client";

import React, { useState, useEffect } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Badge } from "@/components/ui/badge";
import { Skeleton } from "@/components/ui/skeleton";
import { useToast } from "@/components/ui/toast";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Search, Star, MapPin, Phone, Mail, ShoppingCart, Package, Clock, Award, Filter } from "lucide-react";

interface Supplier {
  id: number;
  name: string;
  email: string;
  phone: string;
  location: string;
  rating: number;
  reviews: number;
  specialties: string[];
  products_count: number;
  delivery_time: string;
  min_order: number;
  verified: boolean;
  logo?: string;
  description: string;
  certifications: string[];
}

interface Product {
  id: number;
  name: string;
  category: string;
  price: number;
  unit: string;
  min_quantity: number;
  availability: "in_stock" | "low_stock" | "out_of_stock";
  supplier_id: number;
}

export default function SuppliersMarketplace() {
  const { show } = useToast();
  const [suppliers, setSuppliers] = useState<Supplier[]>([]);
  const [products, setProducts] = useState<Product[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchQuery, setSearchQuery] = useState("");
  const [selectedCategory, setSelectedCategory] = useState("all");
  const [selectedSupplier, setSelectedSupplier] = useState<Supplier | null>(null);
  const [orderItems, setOrderItems] = useState<{[key: number]: number}>({});

  const loadSuppliers = async () => {
    setLoading(true);
    try {
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      const mockSuppliers: Supplier[] = [
        {
          id: 1,
          name: "MediSupply Global",
          email: "orders@medisupply.com",
          phone: "+1-555-0123",
          location: "New York, USA",
          rating: 4.8,
          reviews: 247,
          specialties: ["Pharmaceuticals", "Medical Devices", "Vaccines"],
          products_count: 1250,
          delivery_time: "2-3 days",
          min_order: 500,
          verified: true,
          description: "Leading pharmaceutical supplier with 20+ years experience. ISO certified with global distribution network.",
          certifications: ["ISO 9001", "FDA Approved", "WHO GMP"]
        },
        {
          id: 2,
          name: "PharmaCore Solutions",
          email: "sales@pharmacore.com",
          phone: "+1-555-0456",
          location: "California, USA",
          rating: 4.6,
          reviews: 189,
          specialties: ["Generic Medicines", "OTC Products", "Supplements"],
          products_count: 890,
          delivery_time: "1-2 days",
          min_order: 300,
          verified: true,
          description: "Specialized in generic pharmaceuticals and over-the-counter medications. Fast delivery and competitive pricing.",
          certifications: ["FDA Approved", "cGMP Certified"]
        },
        {
          id: 3,
          name: "BioMed Distributors",
          email: "info@biomed.com",
          phone: "+1-555-0789",
          location: "Texas, USA",
          rating: 4.7,
          reviews: 156,
          specialties: ["Biologics", "Specialty Drugs", "Cold Chain"],
          products_count: 650,
          delivery_time: "3-5 days",
          min_order: 1000,
          verified: true,
          description: "Specialized in temperature-sensitive biologics and specialty pharmaceuticals. Advanced cold chain logistics.",
          certifications: ["ISO 15378", "GDP Certified", "IATA Certified"]
        }
      ];

      const mockProducts: Product[] = [
        { id: 1, name: "Paracetamol 500mg", category: "Pain Relief", price: 0.25, unit: "tablet", min_quantity: 100, availability: "in_stock", supplier_id: 1 },
        { id: 2, name: "Ibuprofen 200mg", category: "Anti-inflammatory", price: 0.18, unit: "tablet", min_quantity: 100, availability: "in_stock", supplier_id: 1 },
        { id: 3, name: "Amoxicillin 250mg", category: "Antibiotics", price: 0.45, unit: "capsule", min_quantity: 50, availability: "low_stock", supplier_id: 2 },
        { id: 4, name: "Omeprazole 20mg", category: "Gastric", price: 0.35, unit: "capsule", min_quantity: 50, availability: "in_stock", supplier_id: 2 },
        { id: 5, name: "Insulin Glargine", category: "Diabetes", price: 25.50, unit: "vial", min_quantity: 10, availability: "in_stock", supplier_id: 3 },
      ];
      
      setSuppliers(mockSuppliers);
      setProducts(mockProducts);
    } catch (err: any) {
      show({
        variant: "destructive",
        title: "Failed to load suppliers",
        description: err?.message || "Unable to fetch suppliers data",
      });
    } finally {
      setLoading(false);
    }
  };

  const handlePlaceOrder = async (supplierId: number) => {
    const orderProducts = products.filter(p => p.supplier_id === supplierId && orderItems[p.id] > 0);
    
    if (orderProducts.length === 0) {
      show({
        variant: "destructive",
        title: "No items selected",
        description: "Please select at least one product to order",
      });
      return;
    }

    try {
      // API call would go here
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      const totalAmount = orderProducts.reduce((sum, product) => 
        sum + (product.price * orderItems[product.id]), 0
      );

      show({
        title: "Order placed successfully!",
        description: `Order for $${totalAmount.toFixed(2)} has been sent to ${suppliers.find(s => s.id === supplierId)?.name}`,
      });

      setOrderItems({});
      setSelectedSupplier(null);
    } catch (err: any) {
      show({
        variant: "destructive",
        title: "Failed to place order",
        description: err?.message || "Unable to place order",
      });
    }
  };

  const updateOrderQuantity = (productId: number, quantity: number) => {
    setOrderItems(prev => ({
      ...prev,
      [productId]: Math.max(0, quantity)
    }));
  };

  const getSupplierProducts = (supplierId: number) => {
    return products.filter(p => p.supplier_id === supplierId);
  };

  const filteredSuppliers = suppliers.filter(supplier =>
    supplier.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
    supplier.specialties.some(s => s.toLowerCase().includes(searchQuery.toLowerCase()))
  );

  useEffect(() => {
    loadSuppliers();
  }, []);

  if (loading) {
    return (
      <div className="p-6">
        <Skeleton className="h-8 w-48 mb-6" />
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {[...Array(6)].map((_, i) => (
            <Skeleton key={i} className="h-64" />
          ))}
        </div>
      </div>
    );
  }

  return (
    <div className="p-6">
      <div className="mb-6">
        <h2 className="text-2xl font-bold text-gray-900 mb-2">Supplier Marketplace</h2>
        <p className="text-gray-600">Find and order from verified pharmaceutical suppliers</p>
      </div>

      {/* Search and Filters */}
      <Card className="mb-6">
        <CardContent className="p-4">
          <div className="flex flex-col md:flex-row gap-4">
            <div className="flex-1 relative">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
              <Input
                placeholder="Search suppliers by name or specialty..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="pl-10 text-black"
              />
            </div>
            <Select value={selectedCategory} onValueChange={setSelectedCategory}>
              <SelectTrigger className="w-48">
                <SelectValue placeholder="Category" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">All Categories</SelectItem>
                <SelectItem value="pharmaceuticals">Pharmaceuticals</SelectItem>
                <SelectItem value="medical_devices">Medical Devices</SelectItem>
                <SelectItem value="supplements">Supplements</SelectItem>
                <SelectItem value="vaccines">Vaccines</SelectItem>
              </SelectContent>
            </Select>
          </div>
        </CardContent>
      </Card>

      {/* Suppliers Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {filteredSuppliers.map((supplier) => (
          <Card key={supplier.id} className="hover:shadow-lg transition-shadow">
            <CardHeader>
              <div className="flex items-start justify-between">
                <div className="flex-1">
                  <CardTitle className="text-lg flex items-center gap-2">
                    {supplier.name}
                    {supplier.verified && (
                      <Badge variant="secondary" className="bg-green-100 text-green-800">
                        <Award className="h-3 w-3 mr-1" />
                        Verified
                      </Badge>
                    )}
                  </CardTitle>
                  <div className="flex items-center gap-1 mt-1">
                    <Star className="h-4 w-4 fill-yellow-400 text-yellow-400" />
                    <span className="text-sm font-medium">{supplier.rating}</span>
                    <span className="text-sm text-gray-500">({supplier.reviews} reviews)</span>
                  </div>
                </div>
              </div>
            </CardHeader>
            <CardContent>
              <p className="text-sm text-gray-600 mb-4">{supplier.description}</p>
              
              <div className="space-y-2 mb-4">
                <div className="flex items-center gap-2 text-sm">
                  <MapPin className="h-4 w-4 text-gray-400" />
                  <span>{supplier.location}</span>
                </div>
                <div className="flex items-center gap-2 text-sm">
                  <Package className="h-4 w-4 text-gray-400" />
                  <span>{supplier.products_count} products</span>
                </div>
                <div className="flex items-center gap-2 text-sm">
                  <Clock className="h-4 w-4 text-gray-400" />
                  <span>{supplier.delivery_time} delivery</span>
                </div>
              </div>

              <div className="mb-4">
                <p className="text-xs text-gray-500 mb-2">Specialties:</p>
                <div className="flex flex-wrap gap-1">
                  {supplier.specialties.slice(0, 3).map((specialty) => (
                    <Badge key={specialty} variant="outline" className="text-xs">
                      {specialty}
                    </Badge>
                  ))}
                </div>
              </div>

              <div className="mb-4">
                <p className="text-xs text-gray-500 mb-2">Certifications:</p>
                <div className="flex flex-wrap gap-1">
                  {supplier.certifications.slice(0, 2).map((cert) => (
                    <Badge key={cert} variant="secondary" className="text-xs bg-blue-100 text-blue-800">
                      {cert}
                    </Badge>
                  ))}
                </div>
              </div>

              <div className="flex gap-2">
                <Dialog>
                  <DialogTrigger asChild>
                    <Button 
                      className="flex-1" 
                      onClick={() => setSelectedSupplier(supplier)}
                    >
                      <ShoppingCart className="h-4 w-4 mr-2" />
                      Order Products
                    </Button>
                  </DialogTrigger>
                  <DialogContent className="max-w-4xl max-h-[80vh] overflow-y-auto bg-white">
                    <DialogHeader>
                      <DialogTitle className="text-black">Order from {supplier.name}</DialogTitle>
                    </DialogHeader>
                    <div className="space-y-4">
                      <div className="grid grid-cols-1 md:grid-cols-2 gap-4 p-4 bg-gray-50 rounded-lg">
                        <div>
                          <p className="text-sm text-gray-600">Contact:</p>
                          <p className="font-medium text-black">{supplier.email}</p>
                          <p className="text-sm text-black">{supplier.phone}</p>
                        </div>
                        <div>
                          <p className="text-sm text-gray-600">Minimum Order:</p>
                          <p className="font-medium text-black">${supplier.min_order}</p>
                          <p className="text-sm text-gray-600">Delivery: {supplier.delivery_time}</p>
                        </div>
                      </div>

                      <div>
                        <h4 className="font-medium mb-3 text-black">Available Products</h4>
                        <div className="space-y-3">
                          {getSupplierProducts(supplier.id).map((product) => (
                            <div key={product.id} className="flex items-center justify-between p-3 border rounded-lg">
                              <div className="flex-1">
                                <h5 className="font-medium text-black">{product.name}</h5>
                                <p className="text-sm text-gray-600">{product.category}</p>
                                <p className="text-sm font-medium text-black">
                                  ${product.price.toFixed(2)} per {product.unit}
                                </p>
                                <p className="text-xs text-gray-500">
                                  Min quantity: {product.min_quantity}
                                </p>
                              </div>
                              <div className="flex items-center gap-2">
                                <Badge 
                                  variant={product.availability === "in_stock" ? "secondary" : "destructive"}
                                  className={product.availability === "in_stock" ? "bg-green-100 text-green-800" : ""}
                                >
                                  {product.availability.replace("_", " ")}
                                </Badge>
                                <Input
                                  type="number"
                                  min="0"
                                  placeholder="Qty"
                                  value={orderItems[product.id] || ""}
                                  onChange={(e) => updateOrderQuantity(product.id, parseInt(e.target.value) || 0)}
                                  className="w-20 text-black"
                                  disabled={product.availability === "out_of_stock"}
                                />
                              </div>
                            </div>
                          ))}
                        </div>
                      </div>

                      <div className="flex justify-between items-center pt-4 border-t">
                        <div>
                          <p className="text-sm text-gray-600">Total Amount:</p>
                          <p className="text-lg font-bold text-black">
                            ${getSupplierProducts(supplier.id).reduce((sum, product) => 
                              sum + (product.price * (orderItems[product.id] || 0)), 0
                            ).toFixed(2)}
                          </p>
                        </div>
                        <Button 
                          onClick={() => handlePlaceOrder(supplier.id)}
                          className="bg-green-600 hover:bg-green-700"
                        >
                          Place Order
                        </Button>
                      </div>
                    </div>
                  </DialogContent>
                </Dialog>
                
                <Button variant="outline" size="sm">
                  <Phone className="h-4 w-4" />
                </Button>
                <Button variant="outline" size="sm">
                  <Mail className="h-4 w-4" />
                </Button>
              </div>
            </CardContent>
          </Card>
        ))}
      </div>

      {filteredSuppliers.length === 0 && (
        <div className="text-center py-12">
          <Package className="h-12 w-12 text-gray-400 mx-auto mb-4" />
          <p className="text-gray-500 mb-4">No suppliers found matching your search</p>
          <Button variant="outline" onClick={() => setSearchQuery("")}>
            Clear Search
          </Button>
        </div>
      )}
    </div>
  );
}